.error-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin: 0 auto;
}

.error-container img {
    width: 52px;
    height: 52px;
}

.error-container span {
    text-align: center;
    font-size: 0.7em;
    font-weight: 100;
    letter-spacing: 0.2em;
}

.error-container pre {
    white-space: pre-wrap;
    text-align: center;
    font-weight: 100;
    line-height: 1.5em;
    font-size: 0.9em;
    padding: 0 1em;
    max-width: 400px;
    word-break: break-all;
    overflow: auto;
}



