.loading-container, .error-container, .no-tasks, .task-list {
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-negative: 1;
        flex-shrink: 1;
}

.loading-container {
    display: -ms-flexbox;
    display: flex;
    height: 200px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.error-container {
    height: 200px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.no-tasks {
    height: 200px;
    font-weight: 100;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.task-list {
    display: grid;
    grid-template: auto/auto;
    grid-gap: 16px;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 30px 0;
}



